}

/* 링크 */
.link-txt {
  position: relative;
}

.link-txt:before {
  position: absolute;
  content: "";
  zoom: 1;
  left: -3px;
  right: -3px;
  bottom: 2px;
  height: 1px;
  background-color: #666;
}

/* 링크 밑줄 */
.link-underline {
  position: relative;
}

.link-underline::after {
  content: "";
  height: 1px;
  position: absolute;
  bottom: -2px;
  left: -3px;
  right: -3px;
  background: #333;
}

