/* ------------------------------------
* svg
*------------------------------------*/
* {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  min-height: 100%;
}

body {
  font: 11px/1.5 "Malgun Gothic", Verdana, Dotum, sans-serif;
  margin: 0;
  letter-spacing: 0;
  color: #000;
}
body select {
  font: 11px/1.5 "Malgun Gothic", Verdana, Dotum, sans-serif;
  margin: 0;
  letter-spacing: 0;
  color: #000;
}
body #wrap #content {
  padding: 0 10px 100px 10px;
  visibility: hidden;
}
body #wrap h1 {
  margin-bottom: 0;
  padding: 10px 15px;
  letter-spacing: -1px;
  font-size: 2.15em;
  font-weight: bold;
}
body #wrap h1 a {
  text-decoration: none;
  color: inherit;
}
body #wrap h1 small {
  display: inline-block;
  margin-left: 5px;
  letter-spacing: -1px;
  font-size: 14px;
}
body #wrap h2 {
  font-size: 11px;
}
body #wrap h3 {
  font-size: 11px;
  color: #999;
}
body #wrap h3.point, body #wrap h3.point * {
  color: #333;
}

body.headerFloating .header {
  position: fixed;
  z-index: 9;
  top: 0px;
  left: 0;
  width: 100%;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.6);
}
body.headerFloating .tab_nav {
  background-color: #efefef;
  transition: all ease-in-out 0.2s;
}
body.headerFloating .info_section {
  border: none;
}
body.headerFloating .info_fold_btn {
  z-index: 4;
  right: 19px;
}
body.headerFloating .info_fold_btn a {
  box-shadow: 0 -2px 10px 2px rgba(0, 0, 0, 0.6);
}
body.headerClose .info_fold_btn a {
  background-color: #ddd;
  color: #fff;
}
body.headerClose.headerFloating .tab_nav {
  border-bottom: none;
  padding-bottom: 1px;
}
body.headerClose.headerFloating .info_fold_btn a {
  background-color: #efefef;
  color: #999;
}

.dimmed {
  z-index: 998;
  display: none;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #000;
  opacity: 0.5;
}

.btn {
  display: inline-block;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
}
.btn span {
  display: block;
  position: relative;
  min-width: 59px;
  margin: 1px 0;
  padding: 2px 2px;
  border: 1px solid #dedede;
  border-radius: 5px;
  text-align: center;
  background-color: #eee;
  color: #000;
}
.btn.active span {
  border: 1px solid #6db8eb;
  background-color: #6db8eb;
}
.btn.active:hover span {
  background-color: #6db8eb;
}
.btn.active span {
  color: #fff;
  cursor: default;
}
.btn.disable span {
  background-color: #eee;
}
.btn.disable span {
  color: #ccc;
}
.btn.disable:hover span {
  background-color: #eee;
}
.btn.disable a {
  cursor: default;
}
.btn:hover span {
  background-color: #ddd;
  transition: all ease-in-out 0.2s;
}
.btn.on span {
  color: #fff;
}
.btn span {
  background-color: #eee;
}
.btn span:after {
  background-color: #eee;
}
.btn.on span {
  background-color: #6db8eb;
  border-color: #6db8eb;
}
.btn.on span:after {
  background-color: #6db8eb;
}
.btn.on:hover span {
  background-color: #6db8eb;
  border-color: #6db8eb;
}
.btn.on:hover span:after {
  background-color: #6db8eb;
}
.btn:hover span {
  background-color: #ddd;
}
.btn:hover span:after {
  background-color: #ddd;
}
.btn.open2.on span {
  background-color: #6db8eb;
  border-color: #6db8eb;
}

.link_box .btn span {
  min-width: 45px;
}

.top_btn {
  float: right;
  margin-right: 18px;
}
.top_btn a, .top_btn a:active, .top_btn a:visited {
  color: #999;
  text-decoration: none;
}

.btn_fold_info {
  position: relative;
  text-align: right;
}
.btn_fold_info a {
  z-index: 8;
  position: absolute;
  top: -1px;
  right: 10px;
  display: inline-block;
  width: 60px;
  height: 22px;
  line-height: 20px;
  text-align: center;
  text-decoration: none;
  font-weight: bold;
  background-color: #f6f6f6;
  color: #999;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  border: 1px solid #efefef;
  border-top: none;
}

body.headerFloating .btn_fold_info a { /*  right:10px;background-color:#f6f6f6; */
  border: 1px solid #ddd;
  border-top: none;
}
body.headerFloating .tab_nav {
  border-bottom: 1px solid #f6f6f6;
}

body[data-headerclose=false] .btn_fold_info a {
  border-top: none;
}

.btn_fold_log,
.btn_fold_depth,
.info_section .btn_close_info {
  position: relative;
  display: inline-block;
  width: 9px;
  height: 9px;
  background-color: #999;
  border-radius: 50%;
  margin-top: 3px;
  margin-right: 3px;
  text-decoration: none;
}
.btn_fold_log i,
.btn_fold_depth i,
.info_section .btn_close_info i {
  position: absolute;
  left: 0px;
  top: 4px;
  display: block;
  width: 9px;
  height: 1px;
  background-color: #ddd;
}
.btn_fold_log i:before,
.btn_fold_depth i:before,
.info_section .btn_close_info i:before {
  content: "";
  display: block;
  position: absolute;
  left: 4px;
  top: -4px;
  width: 1px;
  height: 9px;
  margin-left: 0px;
  background-color: #ddd;
}
.btn_fold_log.on,
.btn_fold_depth.on,
.info_section .on.btn_close_info {
  color: #ddd;
  transition: all ease-in-out 0.26s;
  background-color: #bbb;
}
.btn_fold_log.on i,
.btn_fold_depth.on i,
.info_section .on.btn_close_info i {
  background-color: #fff;
}
.btn_fold_log.on i:before,
.btn_fold_depth.on i:before,
.info_section .on.btn_close_info i:before {
  display: none;
}
h3 .btn_fold_log,
h3 .btn_fold_depth,
h3 .info_section .btn_close_info,
.info_section h3 .btn_close_info {
  float: none !important;
  vertical-align: top;
  margin-top: 4px;
}
th.log .btn_fold_log,
th.log .btn_fold_depth,
th.log .info_section .btn_close_info,
.info_section th.log .btn_close_info {
  margin-right: 6px;
}
th .btn_fold_log.on,
th .btn_fold_depth.on,
th .info_section .on.btn_close_info,
.info_section th .on.btn_close_info {
  color: #bbb;
  background-color: #bbb;
}
th .btn_fold_log:hover, .btn_fold_log:hover,
th .btn_fold_depth:hover,
.btn_fold_depth:hover,
.info_section .btn_close_info:hover {
  color: #666;
  background-color: #666;
}

.btn_close {
  padding: 4px;
  border-radius: 50%;
  background-color: #fff;
  transition: all ease-in-out 0.26s;
}
.btn_close i {
  display: block;
