.header {
  position: relative;
}

.tab_nav {
  overflow: hidden;
  z-index: 100;
  position: relative;
  margin: 0;
  border-bottom: 1px solid #ddd;
  background-color: #fff;
  text-align: left;
  box-shadow: "";
  padding-top: 3px;
}
.tab_nav h2 {
  width: 1px;
  height: 1px;
  position: absolute;
  left: 0;
  right: 0;
  line-height: 1px;
  letter-spacing: -999px;
  font-size: 0;
  color: #fff;
}
.tab_nav .count {
  position: relative;
  top: -1px;
  margin: 0 0 0 5px;
  padding: 1px 5px 2px 5px;
  font-size: 10px;
  font-weight: bold;
  color: #666;
  background: #E5E5E5;
  border-radius: 10px;
  font: 11px verdana, sans-serif;
  line-height: 1.25;
}
.tab_nav ul {
  position: relative;
}
.tab_nav > ul {
  margin-left: 10px;
}
.tab_nav > ul:after {
  content: "";
  clear: both;
  display: table;
}
.tab_nav > ul > li {
  display: table;
  float: left;
  margin-left: 0;
  margin-right: 2px;
}
.tab_nav > ul > li:first-child {
  margin-left: 0;
}
.tab_nav > ul > li > a {
  position: relative;
  display: block;
  margin-bottom: -1px;
  padding: 8px 12px 9px;
  border: 1px solid #d1d1d1;
  border-radius: 3px 3px 0 0;
  font-size: 14px;
  color: #000;
  text-decoration: none;
  background: #fff;
  transition: backgorund ease-in-out 0.2s;
}
.tab_nav > ul > li > a.row2 {
  font-size: 13px;
  padding: 2px 12px 4px;
  line-height: 1.2;
  max-height: 32px;
}
.tab_nav > ul > li.on > a,
.tab_nav > ul > li.on:hover > a {
  border: 1px solid #bbb;
  border-bottom: 1px solid #ddd;
  color: #fff; /*font-weight:bold;*/
  background: #bbb;
}
.tab_nav > ul > li.active > a,
.tab_nav > ul > li.hover > a,
.tab_nav > ul > li:hover > a {
  background: #efefef;
}
.tab_nav > ul > li > a:after {
  position: absolute;
  content: "";
  bottom: -2px;
  left: 50%;
  right: 50%;
  height: 0;
  background-color: #fff;
  transition: backgorund ease-in-out 0.2s;
}
.tab_nav > ul > li.active > a:after,
.tab_nav > ul > li:hover > a:after {
  bottom: 0;
  left: 0;
  right: 0;
  height: 3px;
  background-color: #bbb;
}
.tab_nav > ul > li.on.active > a {
  background-color: #bbb;
}

.tab_nav li.active > .subNav {
  display: block;
}
.tab_nav .subNav {
  display: none;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  margin-left: 10px;
  z-index: 999;
}
.tab_nav .subNav > ul {
  position: relative;
  padding: 0;
}
.tab_nav .subNav > ul:after {
  content: "";
  clear: both;
  display: table;
}
.tab_nav .subNav > ul li {
  float: left;
}
.tab_nav .subNav > ul li a {
  position: relative;
  display: block;
  padding: 5px 7px 7px;
  font-size: 13px;
  color: #666;
  text-decoration: none;
  letter-spacing: -0.6px;
}
.tab_nav .subNav > ul li a:after {
  content: "";
  position: absolute;
  bottom: 0px;
  left: 50%;
  width: 0%;
  height: 2px;
  background-color: #bbb;
}
.tab_nav .subNav > ul li a:hover {
  color: #555;
}
.tab_nav .subNav > ul li a:hover:after,
.tab_nav .subNav > ul li.active a:after {
  left: 0;
  width: 100%;
  transition: all ease-in-out 0.2s;
}
.tab_nav.vertical > ul > li {
  position: relative;
}
.tab_nav.vertical .subNav {
  left: 0;
  top: 40px;
  margin-left: 0px;
  min-width: 150px;
  padding: 10px;
  background: #fff;
  border: 1px solid #ddd;
  border-top: none;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
}
.tab_nav.vertical .subNav > ul li {
  float: none;
}
.tab_nav.vertical .subNav > ul li a {
  padding: 2px 7px 4px;
  font-size: 12px;
  color: #999;
  letter-spacing: -0.8px;
}
.tab_nav.vertical .subNav > ul li a:hover:after,
.tab_nav.vertical .subNav > ul li.active a:after {
  left: 0;
  width: 0;
  transition: none;
}

body.nav_scroll.headerFloating .tab_nav {
